
.navigation {
    width: 100vw;
    position: fixed;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
      0 17px 50px 0 rgba(0, 0, 0, 0.19);

    background-color: rgb(104, 104, 104);

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 10px;

    height: 50px;
    line-height: 50px;

    .title{
        font-size: 1.25em;
        font-weight: 700;
    }
    .navItems{
        div:hover{
            background-color: rgb(136, 135, 135);
        }
        padding-right: 10px;
        display: flex;
        flex-direction: row;
        .item1{
            padding-left: 10px;
            padding-right: 10px;
            font-weight: 600;
            a{  
                color:#44d850;
                text-decoration: none;
            }
        }

        .item2{
            padding-left: 10px;
            padding-right: 10px;
            font-weight: 600;
            a{  
                color:#44d850;
                text-decoration: none;
            }
        }
    }
}