@import url(https://fonts.googleapis.com/css?family=Raleway);
.form{padding-top:100px}.form .login{background-color:gray;border:#000 1px transparent;border-radius:10px;box-shadow:0 6px 16px 0 rgba(0,0,0,.24)}.form .login .labelselect{text-align:center;padding:5px}.form .login .labelselect label{padding:5px}.form .login .submit{display:block;margin-top:10px;margin-left:40%}.form .login p{text-align:center;display:block}
.navigation{width:100vw;position:fixed;padding:0;margin:0;background-color:#686868;display:flex;flex-direction:row;grid-gap:10px;gap:10px;height:50px;line-height:50px}.navigation .title{padding-left:15px;font-size:1.25em;font-weight:700}
.navigation{width:100vw;position:fixed;box-shadow:0 12px 16px 0 rgba(0,0,0,.24),0 17px 50px 0 rgba(0,0,0,.19);background-color:#686868;display:flex;flex-direction:row;justify-content:space-between;grid-gap:10px;gap:10px;height:50px;line-height:50px}.navigation .title{font-size:1.25em;font-weight:700}.navigation .navItems{padding-right:10px;display:flex;flex-direction:row}.navigation .navItems div:hover{background-color:#888787}.navigation .navItems .item1{padding-left:10px;padding-right:10px;font-weight:600}.navigation .navItems .item1 a{color:#44d850;text-decoration:none}.navigation .navItems .item2{padding-left:10px;padding-right:10px;font-weight:600}.navigation .navItems .item2 a{color:#44d850;text-decoration:none}
.BandDiv{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;border-top:2px solid #000}.BandDiv .band{height:200px;width:200px;display:block;padding-left:80px;float:left}.BandDiv .band .image{padding-top:10px;max-width:165px}.BandDiv .band .Title{padding-top:10px;font-size:.9rem;font-weight:300}.BandDiv .band .Title .TitleText{padding-bottom:10px;font-size:1em;font-weight:400}.BandDiv .band .Title a{text-decoration:none;color:#000}.BandDiv .BandTitle{padding-top:10px;margin-left:80px;font-size:1.2rem;font-weight:450}
.rating .ratingBarGreen{margin-top:10px;background-color:#d3d3d3;height:4px;width:100px;position:relative}.rating .ratingBarGreen .green{background-color:#85ce73;height:4px;position:absolute;left:0;bottom:0}.rating .ratingFormStart{padding-top:20px}.rating .labelselect{margin-left:29%;margin-right:29%}.rating .submit{margin-left:34%;margin-right:34%}
body{font-family:"Raleway",sans-serif;margin:0}.pagewrapper{background:#a5eaff;overflow:auto;height:100vh;font-family:"Raleway",sans-serif;display:flex;justify-content:center}.content{width:100vw;padding-top:20px}.error{color:red;font-size:.7em}.button{margin:10px;padding:5px 10px 5px 10px;font-family:"Raleway",sans-serif;color:#fff;font-weight:600;font-size:1.1em;background-color:#2559d3;border:1px solid #000;box-shadow:0 6px 16px 0 rgba(0,0,0,.24);border-radius:5px}.button:hover{cursor:pointer;color:#000;-webkit-transform:scale(1.15);transform:scale(1.15);transition:all .3s ease-in-out 0s}.text,label{font-weight:600 !important;font-size:1.1em}label{font-size:1.25em}input{width:100%;border:1px solid #000;border-radius:3px;font-size:1.25em}p{margin:0px !important}.labelinput{margin-bottom:10px;display:flex;flex-direction:column;justify-content:center}.login{width:320px;padding:20px}.submitbutton{display:flex;justify-content:center}.home{margin-top:48px;width:100vw}.BandDiv{display:flex;flex-direction:column}.bandalbums{flex-direction:row;justify-content:flex-start;margin-bottom:35px}.errorPageTitle{display:flex;justify-content:center;font-size:16em}.errorPageMessage{display:flex;justify-content:center;font-size:1.9em}.errorbtn{display:flex;justify-content:center}a{text-decoration:none}.albumdiv{border:1px none #000;padding:70px 20px 20px 70px;display:flex;flex-direction:row;justify-content:center;grid-gap:45px;gap:45px}.ratingForm .labelinput{margin:0px}.rating input{margin-top:10px !important;margin-bottom:14px !important}.ratingSubmit button{margin-top:0px;margin-left:0px !important}@media only screen and (max-device-width: 1000px){.albumdiv{flex-direction:column;grid-gap:15px;gap:15px}.ratingForm input{width:100px;font-size:.9em}.ratingForm .ratingSubmit{display:flex;justify-content:flex-start}.ratingForm .ratingSubmit button{margin-top:0px}}@media only screen and (max-device-width: 450px){.errorPageTitle{font-size:8em}}.Album{display:flex;flex-direction:row;grid-gap:10px;gap:10px}.tracks{margin-right:25px}.tracks div{display:flex;flex-direction:row;justify-content:space-between}@media only screen and (max-device-width: 600px){.pagewrapper{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.Album{flex-direction:column;margin-bottom:20px}}
