@import url(https://fonts.googleapis.com/css?family=Raleway);
body{
    font-family: "Raleway", sans-serif;
    margin: 0;
}
.pagewrapper{
  background: rgb(165, 234, 255);
  overflow: auto;
  height: 100vh;
  font-family: "Raleway", sans-serif;
  display: flex;
  justify-content: center;
}
.content{
    width: 100vw;
    padding-top: 20px;
}

.error {
    color: red;
    font-size: 0.7em;
  }

.button {
    margin: 10px;
    padding:5px 10px 5px 10px;
    font-family: "Raleway", sans-serif;
    color:rgb(255, 255, 255);
    font-weight: 600;
    font-size: 1.1em;
    background-color: #2559d3;
    border: 1px solid black;
    box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.24);
    border-radius: 5px;
}

.button:hover {
    cursor: pointer;
    color: rgb(0, 0, 0);
    transform: scale(1.15);
    transition: all 0.3s ease-in-out 0s;
}

.text, label {
    font-weight: 600 !important;
    font-size: 1.1em;
}
label{
    font-size:1.25em;
}
input {
    width: 100%;
    border: 1px solid black;
    border-radius: 3px;
    font-size: 1.25em;
  }
  p{
      margin: 0px !important;
  }

.labelinput{
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.login{
    width: 320px;
    padding: 20px;
}
.submitbutton{
    display:flex;
    justify-content: center;
}
.home{
    margin-top: 48px;
    width: 100vw;
}

.BandDiv{
    display:flex;
    flex-direction: column;
}

.bandalbums{
    flex-direction: row;
    justify-content: flex-start;
    margin-bottom: 35px;
}

.errorPageTitle {
    display: flex;
    justify-content: center;
    font-size: 16em;
}
.errorPageMessage{
    display: flex;
    justify-content: center;
font-size: 1.9em;
}
.errorbtn{
    display: flex;
    justify-content: center;
}
a {
    text-decoration: none;
}
.albumdiv{
    border: 1px none black;
    padding: 70px 20px 20px 70px ;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 45px;
}
.ratingForm{
    .labelinput{
        margin: 0px;
    }
}
.rating{    
    input{
        margin-top: 10px !important;
        margin-bottom: 14px !important;
    }
}
    
.ratingSubmit{
    button{
        margin-top: 0px;
        margin-left: 0px !important;
    }
}

@media only screen 
and (max-device-width : 1000px) {
    .albumdiv{
        flex-direction: column;
        gap: 15px;
    }
    .ratingForm {
        input{
            width: 100px;
            font-size: 0.9em;
        }
        .ratingSubmit{
            display: flex;
            justify-content: flex-start;
            button{
                margin-top: 0px;
            }
        }
    }
}
@media only screen 
and (max-device-width : 450px) {
    .errorPageTitle {
        font-size: 8em;
    }
}
.Album{
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.tracks{
    margin-right: 25px;
    div{    
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}

@media only screen 
and (max-device-width : 600px) {
    .pagewrapper{
        height: fit-content;
    }
    .Album{
        flex-direction: column;
        margin-bottom: 20px;
    }

}