.BandDiv {
    height: fit-content;
    border-top: 2px solid black;
    .band {
        height: 200px;
        width: 200px;
        display: block;
        padding-left: 80px;
        float: left;
        .image{
            padding-top: 10px;
            max-width: 165px;
        }
        .Title{
            padding-top: 10px;
            .TitleText{
                padding-bottom: 10px;
                font-size: 1em;
                font-weight: 400;
            }
            a{
                text-decoration: none;
                color: black;
            }
            font-size: 0.9rem;
            font-weight: 300;
        }    
    }
    
    .BandTitle {
        padding-top: 10px;
        margin-left: 80px;
        font-size: 1.2rem;
        font-weight: 450;
    }
}